  transform-origin: center top;
  -webkit-animation: slide-top 0.3s;
  animation: slide-top 0.3s;
}
.vmc-slide-top-leave-active {
  -webkit-transform-origin: center top;
  transform-origin: center top;
  -webkit-animation: slide-top 0.3s reverse;
  animation: slide-top 0.3s reverse;
}
.vmc-fade-enter-active {
  -webkit-animation: fade .3s;
  animation: fade .3s;
}
.vmc-fade-leave-active {
  -webkit-transform-origin: center top;
  transform-origin: center top;
  -webkit-animation: fade .3s reverse;
  animation: fade .3s reverse;
}
.vmc-fade-scale-enter-active {
  -webkit-transform-origin: center top;
  transform-origin: center top;
  -webkit-animation: fade-scale 0.3s;
  animation: fade-scale 0.3s;
}
.vmc-fade-scale-leave-active {
